<template
	lang="html"
>
	<fragment>
		<!-- if no name is provided it's the first level, create the root list -->
		<ul
			v-if="name==''"
			class="language-list"
		>
			<LanguageItem
				v-for="(value, key) in data"
				:key="key"
				:name="key"
				:data="value"
			/>
		</ul>
		<!-- if a name is provided it's a child, show the name and all possible childs -->
		<li
			v-else
			class="language-list__item"
		>
			<p class="language-list__name">
				{{ name }}
			</p>
			<p
				v-if="typeof data === 'string'"
				class="language-list__note"
			>
				{{ data }}
			</p>
			<ul
				v-if="typeof data ==='object'"
				class="language-list"
			>
				<LanguageItem
					v-for="(value, key) in data"
					:key="key"
					:name="key"
					:data="value"
				/>
			</ul>
		</li>
	</fragment>
</template>

<script lang="js">

import { Fragment } from 'vue-fragment';

export default {
	name: 'LanguageItem',
	components: {
		Fragment
	},
	props: {
		data: {
			type: [ Object, String ],
			required: true
		},
		name : {
			type: String,
			required: false,
			default: ''
		}
	},
	computed: {
		isNested() {
			return typeof this.data == 'object';
		}
	}
};


</script>

